<template>
  <div class="detail">
    <h1>{{ movie.name }}</h1>
    <img :src="movie.poster" class="poster" />
    <section>
      <h2>영화정보</h2>
      <dl class="info">
        <dt>감독</dt>
        <dd>{{ movie.director }}</dd>
        <dt>출연</dt>
        <dd>{{ movie.actors }}</dd>
        <dt>러닝타임</dt>
        <dd>{{ movie.time }}</dd>
      </dl>
    </section>
    <section>
      <h2>줄거리</h2>
      <p v-html="movie.synopsis" class="synopsis"></p>
    </section>
    <router-link :to="{ name: 'index', params: { id: movie.id } }" class="link"
      >돌아가기</router-link
    >
  </div>
</template>

<script>
import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";

export default {
  setup() {
    const movie = ref({});

    const router = useRouter();
    const id = router.currentRoute.value.params.id;

    onMounted(() => {
      axios.get(`/api/movies/${id}`).then((response) => {
        movie.value = response.data[0];
      });
    });

    return {
      movie,
    };
  },
};
</script>
